import Vue from "vue";
import Vuex from "vuex";
import "core-js/es6/symbol";
import axios from "axios";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    side: Boolean,
    sideOpened: Boolean,
    cards:[],
  },
  mutations: {
    openSidebar(state) {
      state.sideOpened = true;
      state.side = true;
    },
    closeSidebar(state) {
      state.sideOpened = false;
      state.side = false;
    },
    toggleSideBar(state) {
      state.sideOpened = !state.sideOpened;
    },
    cards(state,payload){
      state.cards=payload
    }
  },
  actions: {
    getTabs({commit}) {
      return new Promise((resolve, reject) => {
        axios
          .get("https://prime-crowd.com/api/mock/rounds")
          .then((res) => {
            commit("cards",res.data);
            resolve(res.data);
          })
          .catch((err) => {
            reject(err);
          });
      });
    },
  },
  modules: {},
});
